.app-wrapper{
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-areas: 
  "h"
  "c";
  grid-template-rows: 70px auto;
  grid-gap: 20px;
}
.app-wrapper-content{
  grid-area: c;
  justify-self:center;
}
.mainContent, .profilePage, .inProfileNotPosts {
  width: 800px;
  min-height: 205px;
  height: max-content;
  background-color:#fff;
  box-shadow: 0px 5px 10px 2px #3535352c;
  color: #353535;
  border-radius: 0px 0px 25px 25px;
  padding: 5px;
  margin: 0 200px;
  transition: 2.5s;
}
.profilePage {
  border-radius: 0;
}
.inProfileNotPosts {
  border-radius: 0 0 35%/10%;
}
::-webkit-scrollbar {
  width: 0px;
}
input:-webkit-autofill {
  box-shadow: inset 0 0 0 50px white;
}

.checkbox {
  display: flex;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.checkbox input {
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.checkbox input:checked ~ .checkboxCheck {
  background-color: #c4f974;
}
.checkbox input:checked ~ .checkboxCheck:after {
  left: 28px;
}
.checkbox:hover input ~ .checkboxCheck {
  background-color: #eee;
}
.checkbox:hover input:checked ~ .checkboxCheck {
  background-color: #c4f974;
}
.checkbox .checkboxCheck {
  top: 1px;
  left: 0;
  height: 22px;
  width: 40px;
  background-color: #eee;
  transition: background-color 0.25s ease;
  border-radius: 11px;
}
.checkbox .checkboxCheck:after {
  content: "";
  position: absolute;
  left: 10px;
  top: 3px;
  width: 16px;
  height: 16px;
  display: block;
  background-color: #fff;
  border-radius: 50%;
  transition: left 0.25s ease;
}
.checkbox .checkboxCheck {
  color: #333;
  line-height: 1.4;
  font-size: 16px;
  transition: color 0.25s ease;
}